.carousel-list{
    text-align: center;
    font-size: 1.5rem;
    overflow: hidden;
    width: 80%;
}
.cards-popular{
    width: 100%;
}
.card-popular{
    margin-bottom: 1rem;
    p{
        font-size: 2rem;
        margin: 0;
        padding: 1rem;
        text-transform: uppercase;
    }
}
.imagen-container-popular{
    img{
        border-radius: 10px;
        width: 80%;
        object-fit: cover;
    }
}
.card-popular-text{
    height: 20%;
}
.PopularProducts{
    text-align: center;
}
.tituloPopular{
    text-transform: uppercase;
    font-size: 3rem;
}
.imagen-container-popular:hover{
    opacity: .8;
}
.tabuladores .glider-dot{

    &:hover{
        background-color: #2EC4B6;
    }
    height: 1rem;
    width: 2rem;

}
.tabuladores{
    .active{
        background-color: #2EC4B6;
    }
}