.iconos--beneficios--container{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(12rem,1fr));
    grid-auto-flow: dense;
    margin: 2rem 0;
    padding: 2rem 1rem;
    background-color:#343A40;
    .iconos--beneficios--container--item{
        img{
            height: 10rem;
        }
        p{
            text-align: center;
            font-size: 1.5rem;
            text-transform: capitalize;
            padding: 0 2rem;
        }
    }
}
