.newsletter{
    color: #212529;
    background-color: #F8F9FA;
    width: 100%;
    display: flex;
    justify-content: center;
}
.newsletter--container{
    padding: 10rem 1rem;
    width: 80%;
}

.newsletter--container--text{
    text-align: start;
    font-size: 1.5rem;
}
.newsletter--iconos{
    display: flex;
    justify-content: start;
    gap: 2rem;
    margin: 2rem 0;
    height: 1rem;
    list-style: none;
    padding: 0;    
}
.newsletter--container--text--subscribe{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    h3{
        margin-top: 10rem;
        font-size: 2rem;
        font-weight: 400;
        text-align: start;
        text-transform: uppercase;
        margin-bottom: 0;
    }
    p{
        font-size: 1.5rem;
        text-align: start;
        margin-bottom: 3rem;
    }
    input{
        color: #212529;
        background-color: transparent;
        border: none;
        border-bottom: 2px solid #212529;
        margin-bottom: 2rem;
        font-size: 2rem;
        width: 100%;
        outline: none;
    }
    button{
        padding: 1rem;
        background-color: #6C757D;
        color: white;
        border: none;
        border-radius: .5rem;
        height: 4rem;
        font-size: 2rem;
        cursor: pointer;
    }
}
@media (min-width: 750px){
    .newsletter{
        width: 100%;
        display: flex;
        justify-content: center;
    }
    .newsletter--container{
        width: 60%;
    }
}