.opiniones{
    margin: 7rem 0;
}
.opiniones--comillas{
    color: #2EC4B6;
}
.opiniones--container{
    overflow: hidden;
}
.opiniones--opinion{
    padding: 1rem;
    p{
        font-size: 3rem;
    }
    .opiniones--opinion--autor{
        font-size: 1.6rem;
        span{
            font-size: 2.6rem;
            color: #2EC4B6;
        }
    }
}
.opiniones--tabulador .glider-dot {
    height: 1rem;
    width: 2rem;
    &:hover{
        background-color: #2EC4B6;
    }
}
.opiniones--tabulador{
    margin-top: 3rem;
    .active{
        background-color: #2EC4B6;
    }
}
@media (min-width: 765px){
    .opiniones{
        display: flex;
        justify-content: center;
        width: 100%;
        flex-direction: column;
        .opiniones--container{
            width: 40%;
        }
    }
}