.blog{
    background-color: #212529;
    color: #F8F9FA;
    width: 100%;
    h3{
        font-size: 3rem;
        margin: 0;
        margin-bottom: 1rem;
        text-transform: uppercase;
    }
    p{
        font-size: 2rem;
    }
    margin-bottom: 2rem;
}
.blog--container{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));
}
.blog--container--item{
    width: 100%;
}
.blog--img--container{
    width: 100%;
    img{
        width: 80%;
    }
}
.blog--text{
    padding: 2rem;
    p{
        font-size: 2rem;
        text-align: start;
        margin: .5rem 0;
        padding: 0;
    }
    span{
        font-size: 3rem;
        font-weight: bold;
        margin-right: 1rem;
        color: #2EC4B6;
    }
    .text--flex{
        p{
            margin: 0;
            padding: 0;
        }
        span{
            color: #2EC4B6;
            font-size: 2rem;
            font-weight: 400;
        }
        span:hover{
            color: black;
            cursor: pointer;
        }
    }
    .blog-parrafo{
        margin: .5rem 0;
        font-size: 1.5rem;
    }
}
.blog--leerMas{
    text-align: start;
    a{
        color: #2EC4B6;
        font-size: 2rem;
        text-decoration: none;
        cursor: pointer;
    }
    a:hover{
        color: black;
    }
}
@media(min-width: 750px){
    .blog--container{
        grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
        grid-auto-flow: dense;
        gap: 1rem;
        .n1{
            grid-column: 1/3;
            grid-row: 1/1;
        }
        .n2{
            grid-column: 3/6;
            grid-row: 1/1;
        }
        .n3{
            grid-column:6/8;
            grid-row: 1/1;
        }
        .n4{
            grid-column: 1/3;
            grid-row: 2/3;
        }
        .n5{
            grid-column: 3/8;
            grid-row: 2/4;
        }
        .n6{
            grid-column: 1/3;
            grid-row: 3/4;
        }
    }
    .blog{
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
    }
    .blog--container{
        width: 60%;
    }
}