.cuadro-debajo {
  padding: 2rem 0;
  background-color: #343A40;
  color: #F8F9FA;
  text-align: center;
  margin-top: 6rem;
}

.cuadro-debajo h3 {
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 900;
}

.cuadro-debajo p {
  font-size: 1.5rem;
  padding: 0 2rem;
}

.cuadro-debajo p span {
  font-weight: 900;
  text-transform: uppercase;
}

/*Rubik 300, 400 , 900 */
.grid-titles {
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
}

.list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 5rem 0;
  gap: 1.5rem;
  justify-content: center;
  width: 80%;
}

.list li {
  font-size: 1.5rem;
  text-align: center;
  font-weight: 300;
  text-transform: capitalize;
}

.list .active {
  color: #2EC4B6;
  border-bottom: 3px solid #2EC4B6;
}

.list li:hover {
  color: #2EC4B6;
  cursor: pointer;
}

.GridItems {
  width: 100%;
  display: flex;
  justify-content: center;
}

.cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
  gap: 1rem;
  grid-auto-flow: dense;
  width: 80%;
}

.card {
  width: 100%;
}

.imagen-container {
  width: 100%;
}

.img {
  width: 100%;
}

.card p {
  width: 100%;
  padding: 0 2rem;
  font-size: 2rem;
  cursor: pointer;
}

.text-container {
  display: flex;
  justify-content: space-between;
  padding-right: 2rem;
}

.text-container p {
  font-size: 1.5rem;
  cursor: pointer;
}

.container--icons {
  height: 3rem;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.container--icons .other--icons {
  display: flex;
  justify-content: center;
  gap: 3rem;
  padding-left: 1rem;
}

.hamburguer--icon {
  padding-right: 1rem;
}

.logo {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.logo img {
  margin-top: 1.5rem;
  height: 3rem;
  cursor: pointer;
  margin-bottom: 5rem;
}

.barra--menu {
  display: none;
  justify-content: space-between;
  align-items: center;
}

.barra--menu--logo {
  padding-left: 2rem;
  width: 25rem;
}

.barra--menu--logo img {
  padding-top: 2rem;
  width: 100%;
  height: 100%;
}

.barra--menu--items {
  padding-right: 2rem;
  display: flex;
  gap: 3rem;
  align-items: center;
  height: 100%;
}

.barra--menu--items a {
  color: #F8F9FA;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 2rem;
}

.barra--menu--items .active {
  border-bottom: 3px solid #2EC4B6;
}

.barra--menu--item:hover {
  border-bottom: 3px solid #2EC4B6;
}

@media (min-width: 800px) {
  .logo {
    display: none;
  }

  .barra--menu {
    display: flex;
  }
}
.relative {
  color: #F8F9FA;
  background-color: #343A40;
  padding: 1rem;
  position: relative;
  overflow-y: hidden;
}

.triangle {
  height: 0;
  width: 0;
  border-right: 5rem solid #2EC4B6;
  border-top: 5rem solid transparent;
  border-bottom: 5rem solid transparent;
  position: absolute;
  right: 0;
  top: -5rem;
}

.triangle2 {
  height: 0;
  width: 0;
  border-left: 5rem solid #2EC4B6;
  border-top: 5rem solid transparent;
  border-bottom: 5rem solid transparent;
  position: absolute;
  left: 0;
  top: -5rem;
}

.text h3 {
  font-size: 3rem;
  text-align: center;
  text-transform: uppercase;
}

.text p {
  font-size: 2rem;
  text-align: center;
}

.anterior, .siguiente {
  height: 3rem;
  margin: 0;
  padding: 0;
  background-color: transparent;
  border: none;
}
.anterior img, .siguiente img {
  height: 100%;
  width: 100%;
}

.carousel-list {
  text-align: center;
  font-size: 1.5rem;
  overflow: hidden;
  width: 80%;
}

.cards-popular {
  width: 100%;
}

.card-popular {
  margin-bottom: 1rem;
}
.card-popular p {
  font-size: 2rem;
  margin: 0;
  padding: 1rem;
  text-transform: uppercase;
}

.imagen-container-popular img {
  border-radius: 10px;
  width: 80%;
  object-fit: cover;
}

.card-popular-text {
  height: 20%;
}

.PopularProducts {
  text-align: center;
}

.tituloPopular {
  text-transform: uppercase;
  font-size: 3rem;
}

.imagen-container-popular:hover {
  opacity: 0.8;
}

.tabuladores .glider-dot {
  height: 1rem;
  width: 2rem;
}
.tabuladores .glider-dot:hover {
  background-color: #2EC4B6;
}

.tabuladores .active {
  background-color: #2EC4B6;
}

.iconos--beneficios--container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(12rem, 1fr));
  grid-auto-flow: dense;
  margin: 2rem 0;
  padding: 2rem 1rem;
  background-color: #343A40;
}
.iconos--beneficios--container .iconos--beneficios--container--item img {
  height: 10rem;
}
.iconos--beneficios--container .iconos--beneficios--container--item p {
  text-align: center;
  font-size: 1.5rem;
  text-transform: capitalize;
  padding: 0 2rem;
}

.blog {
  background-color: #212529;
  color: #F8F9FA;
  width: 100%;
  margin-bottom: 2rem;
}
.blog h3 {
  font-size: 3rem;
  margin: 0;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
.blog p {
  font-size: 2rem;
}

.blog--container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(12rem, 1fr));
}

.blog--container--item {
  width: 100%;
}

.blog--img--container {
  width: 100%;
}
.blog--img--container img {
  width: 80%;
}

.blog--text {
  padding: 2rem;
}
.blog--text p {
  font-size: 2rem;
  text-align: start;
  margin: 0.5rem 0;
  padding: 0;
}
.blog--text span {
  font-size: 3rem;
  font-weight: bold;
  margin-right: 1rem;
  color: #2EC4B6;
}
.blog--text .text--flex p {
  margin: 0;
  padding: 0;
}
.blog--text .text--flex span {
  color: #2EC4B6;
  font-size: 2rem;
  font-weight: 400;
}
.blog--text .text--flex span:hover {
  color: black;
  cursor: pointer;
}
.blog--text .blog-parrafo {
  margin: 0.5rem 0;
  font-size: 1.5rem;
}

.blog--leerMas {
  text-align: start;
}
.blog--leerMas a {
  color: #2EC4B6;
  font-size: 2rem;
  text-decoration: none;
  cursor: pointer;
}
.blog--leerMas a:hover {
  color: black;
}

@media (min-width: 750px) {
  .blog--container {
    grid-template-columns: repeat(auto-fit, minmax(25rem, 1fr));
    grid-auto-flow: dense;
    gap: 1rem;
  }
  .blog--container .n1 {
    grid-column: 1/3;
    grid-row: 1/1;
  }
  .blog--container .n2 {
    grid-column: 3/6;
    grid-row: 1/1;
  }
  .blog--container .n3 {
    grid-column: 6/8;
    grid-row: 1/1;
  }
  .blog--container .n4 {
    grid-column: 1/3;
    grid-row: 2/3;
  }
  .blog--container .n5 {
    grid-column: 3/8;
    grid-row: 2/4;
  }
  .blog--container .n6 {
    grid-column: 1/3;
    grid-row: 3/4;
  }

  .blog {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .blog--container {
    width: 60%;
  }
}
.newsletter {
  color: #212529;
  background-color: #F8F9FA;
  width: 100%;
  display: flex;
  justify-content: center;
}

.newsletter--container {
  padding: 10rem 1rem;
  width: 80%;
}

.newsletter--container--text {
  text-align: start;
  font-size: 1.5rem;
}

.newsletter--iconos {
  display: flex;
  justify-content: start;
  gap: 2rem;
  margin: 2rem 0;
  height: 1rem;
  list-style: none;
  padding: 0;
}

.newsletter--container--text--subscribe {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.newsletter--container--text--subscribe h3 {
  margin-top: 10rem;
  font-size: 2rem;
  font-weight: 400;
  text-align: start;
  text-transform: uppercase;
  margin-bottom: 0;
}
.newsletter--container--text--subscribe p {
  font-size: 1.5rem;
  text-align: start;
  margin-bottom: 3rem;
}
.newsletter--container--text--subscribe input {
  color: #212529;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #212529;
  margin-bottom: 2rem;
  font-size: 2rem;
  width: 100%;
  outline: none;
}
.newsletter--container--text--subscribe button {
  padding: 1rem;
  background-color: #6C757D;
  color: white;
  border: none;
  border-radius: 0.5rem;
  height: 4rem;
  font-size: 2rem;
  cursor: pointer;
}

@media (min-width: 750px) {
  .newsletter {
    width: 100%;
    display: flex;
    justify-content: center;
  }

  .newsletter--container {
    width: 60%;
  }
}
.opiniones {
  margin: 7rem 0;
}

.opiniones--comillas {
  color: #2EC4B6;
}

.opiniones--container {
  overflow: hidden;
}

.opiniones--opinion {
  padding: 1rem;
}
.opiniones--opinion p {
  font-size: 3rem;
}
.opiniones--opinion .opiniones--opinion--autor {
  font-size: 1.6rem;
}
.opiniones--opinion .opiniones--opinion--autor span {
  font-size: 2.6rem;
  color: #2EC4B6;
}

.opiniones--tabulador .glider-dot {
  height: 1rem;
  width: 2rem;
}
.opiniones--tabulador .glider-dot:hover {
  background-color: #2EC4B6;
}

.opiniones--tabulador {
  margin-top: 3rem;
}
.opiniones--tabulador .active {
  background-color: #2EC4B6;
}

@media (min-width: 765px) {
  .opiniones {
    display: flex;
    justify-content: center;
    width: 100%;
    flex-direction: column;
  }
  .opiniones .opiniones--container {
    width: 40%;
  }
}
.imagenes--bottom {
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  margin: 3rem 0;
}
.imagenes--bottom .imagenes--bottom--container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
  gap: 1rem;
  width: 90%;
}
.imagenes--bottom .imagenes--bottom--container img {
  height: 100%;
  width: 100%;
}

@media (min-width: 625px) {
  .imagenes--bottom .imagenes--bottom--container {
    grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
  }
}
.logo--footer {
  width: 100%;
  display: flex;
  justify-content: start;
}
.logo--footer img {
  width: 50%;
  height: 100%;
}

.footer--container {
  background-color: #212529;
  color: #F8F9FA;
  margin-bottom: 0;
  padding: 2rem;
}

.iconos--footer--container--item {
  display: flex;
  gap: 2rem;
  margin-bottom: 3rem;
  align-items: center;
}
.iconos--footer--container--item p {
  font-size: 2rem;
}

.derechos-reservados {
  font-size: 1.3rem;
  text-align: center;
  margin-top: 3rem;
}

@media (min-width: 765px) {
  .iconos--footer {
    display: flex;
    width: 100%;
    justify-content: center;
  }

  .logo--footer {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 5rem;
  }
  .logo--footer img {
    width: 30rem;
  }

  .iconos--footer--container--item {
    font-size: 1.5rem;
  }
  .iconos--footer--container--item p {
    font-size: 1.5rem;
  }
}
.Front--Image {
  background-color: #dddddd;
}

.Front--Div--Container {
  overflow-x: hidden;
  width: 100%;
}

.FrontDiv--text {
  padding-left: 1rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 90%;
}

.FrontDiv--text {
  overflow-x: hidden;
  color: #212529;
}
.FrontDiv--text .precio {
  font-size: 2rem;
  margin-bottom: 2rem;
  font-weight: 600;
}
.FrontDiv--text .parrafo {
  font-size: 1.5rem;
  font-weight: 400;
  text-transform: capitalize;
  text-transform: uppercase;
  padding-right: 1rem;
}
.FrontDiv--text .lema {
  font-size: 3rem;
  text-transform: capitalize;
  text-transform: uppercase;
  font-weight: 900;
}
.FrontDiv--text .comprar {
  margin-top: 3rem;
  font-family: "Rubik", sans-serif;
  margin-bottom: 1rem;
  font-weight: 400;
  background-color: #2EC4B6;
  border: none;
  color: #FDFFFC;
  border-radius: 1rem;
  cursor: pointer;
  width: 50%;
  height: 3rem;
  font-size: 1.5rem;
  text-transform: capitalize;
}

@media (min-width: 1624px) {
  .Front--Image {
    background: #dddddd url("../../Img/jpg/imagenes--bottom/bucket5.jpg") bottom left no-repeat;
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;
    min-height: 70vh;
  }

  .FrontDiv--text {
    width: 95%;
    align-items: flex-end;
    justify-content: center;
    position: absolute;
  }
  .FrontDiv--text .precio {
    font-size: 6rem;
    margin-bottom: 2rem;
  }
  .FrontDiv--text .parrafo {
    font-size: 2rem;
    font-weight: 400;
    text-transform: capitalize;
    text-transform: uppercase;
    padding-right: 1rem;
  }
  .FrontDiv--text .lema {
    font-size: 8rem;
    text-transform: capitalize;
    text-transform: uppercase;
    font-weight: 900;
  }
  .FrontDiv--text .comprar {
    width: 30%;
    margin-top: 3rem;
    font-family: "Rubik", sans-serif;
    margin-bottom: 1rem;
    font-weight: 400;
    background-color: #2EC4B6;
    border: none;
    color: #FDFFFC;
    border-radius: 1rem;
    cursor: pointer;
    width: 50%;
    height: 5rem;
    font-size: 2rem;
    text-transform: uppercase;
  }
}
@media (min-width: 625px) {
  .cuadro-debajo h3 {
    font-size: 3rem;
  }
  .cuadro-debajo p {
    font-size: 1.6rem;
  }
}
.text-container p:hover {
  color: #2EC4B6;
}

.card p:hover {
  color: #2EC4B6;
}

.imagen-container {
  cursor: pointer;
}
.imagen-container:hover {
  opacity: 0.9;
}

.historia--black--grade--img {
  display: none;
}

@media (min-width: 1024px) {
  .historia--black--grade {
    width: 100%;
    display: flex;
    align-items: center;
    height: 50rem;
    overflow: hidden;
  }
  .historia--black--grade .historia--black--grade--img {
