.historia--black--grade--img{
    display: none;
}
@media (min-width:1024px){
    .historia--black--grade{
        width: 100%;
        display: flex;
        align-items: center;
        height: 50rem;
        overflow: hidden;
        .historia--black--grade--img{
            display: block;
            height: 100%;

            img{
                height: 100%;
            }
        }
        .relative{
            height: 100%;
            .text{
                h3{
                    font-size: 3rem;
                }
                p{
                    font-size: 2rem;
                }
            }
        }
    }
    
}
