.cuadro-debajo{
    padding: 2rem 0;
    background-color: #343A40;
    color:#F8F9FA;
    text-align: center;
    margin-top: 6rem;
}
.cuadro-debajo h3{
    font-size: 2rem;
    text-transform: uppercase;
    font-weight: 900;
}
.cuadro-debajo p{
    font-size: 1.5rem;
    padding: 0 2rem;
}
.cuadro-debajo p span{
    font-weight: 900;
    text-transform: uppercase;
}
/*Rubik 300, 400 , 900 */
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;900&display=swap');

.grid-titles{
    display: flex;
    justify-content: center;
    width: 100%;
    flex-wrap: wrap;
}


.list{
    display: flex;
    list-style: none;
    margin: 0;
    padding: 5rem 0;
    gap: 1.5rem;
    justify-content: center;
    width: 80%;
}

.list li{
    font-size: 1.5rem;
    text-align: center;
    font-weight: 300;
    text-transform: capitalize;

}
.list .active{
  color: #2EC4B6;
  border-bottom: 3px solid #2EC4B6;
}

.list li:hover{
    color: #2EC4B6;
    cursor: pointer;
}


.GridItems{
    width: 100%;
    display: flex;
    justify-content: center;
}

.cards{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
    gap: 1rem;
    grid-auto-flow: dense;
    width: 80%;
}
.card{
  width: 100%;
}
.imagen-container{
    width: 100%;
}
.img{
  width: 100%;
}
.card p{
    width: 100%;
    padding: 0 2rem;
    font-size: 2rem;
    cursor: pointer;
}
.text-container{
    display: flex;
    justify-content: space-between;
    padding-right:2rem;
}
.text-container p{
    font-size: 1.5rem;
    cursor: pointer;
}
.container--icons{
    height: 3rem;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 2rem;
}
.container--icons .other--icons{
  display: flex;
  justify-content: center;
  gap: 3rem;
  padding-left: 1rem;
}
.hamburguer--icon{
  padding-right: 1rem;
}

.logo{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.logo img{
    margin-top: 1.5rem;
    height: 3rem;
    cursor: pointer;
    margin-bottom: 5rem;
}
.barra--menu{
  display: none;
  justify-content: space-between;
  align-items: center;
}
.barra--menu--logo{
  padding-left: 2rem;
  width: 25rem;
}
.barra--menu--logo img{
  padding-top: 2rem;
  width: 100%;
  height: 100%;
}
.barra--menu--items{
  padding-right: 2rem;
  display: flex;
  gap: 3rem;
  align-items: center;
  height: 100%;
}
.barra--menu--items a{
  color: #F8F9FA;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 2rem;
}
.barra--menu--items .active{
  border-bottom: 3px solid #2EC4B6;
}
.barra--menu--item:hover{
  border-bottom: 3px solid #2EC4B6;
}

@media (min-width: 800px){
  .logo{
    display: none;
  }
  .barra--menu{
    display: flex;
  }
}

.relative{
    color: #F8F9FA;
    background-color: #343A40;
    padding: 1rem;
    position: relative;
    overflow-y: hidden;
}
.triangle{
    height: 0;
    width: 0;
    border-right: 5rem solid #2EC4B6;
    border-top: 5rem solid transparent;
    border-bottom: 5rem solid transparent;
    position: absolute;
    right: 0;
    top:-5rem;
}
.triangle2{
  height: 0;
  width: 0;
  border-left: 5rem solid #2EC4B6;
  border-top: 5rem solid transparent;
  border-bottom: 5rem solid transparent;
  position: absolute;
  left: 0;
  top:-5rem;
}
.text h3{
    font-size: 3rem;
    text-align: center;
    text-transform: uppercase;
}
.text p{
    font-size: 2rem;
    text-align: center;
}
