.Front--Image{
    background-color: #dddddd;
}
.Front--Div--Container{
  overflow-x: hidden;
  width: 100%;
}
.FrontDiv--text{
  padding-left: 1rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 90%;
}
.FrontDiv--text{
  overflow-x: hidden;
    color: #212529;
    .precio{
        font-size: 2rem;
        margin-bottom: 2rem;
        font-weight: 600;
    }
    .parrafo{
        font-size: 1.5rem;
        font-weight: 400;
        text-transform: capitalize;
        text-transform: uppercase;
        padding-right: 1rem;
      }
    .lema{
        font-size: 3rem;
        text-transform: capitalize;
        text-transform: uppercase;
        font-weight: 900;
      }
      .comprar{
        margin-top: 3rem;
        font-family: 'Rubik', sans-serif;
        margin-bottom: 1rem;
        font-weight: 400;
        background-color: #2EC4B6;
        border: none;
        color: #FDFFFC;
        border-radius: 1rem;
        cursor: pointer;
        width: 50%;
        height: 3rem;
        font-size: 1.5rem;
        text-transform: capitalize;
    }
}

@media(min-width: 1624px){
    
    .Front--Image{
        background: #dddddd url('../../Img/jpg/imagenes--bottom/bucket5.jpg') bottom left no-repeat;
        background-repeat: no-repeat;
        background-size: contain;
        position: relative;
        min-height: 70vh;
    }
    .FrontDiv--text{
        width: 95%;
        align-items: flex-end;
        justify-content: center;
        position: absolute;
        .precio{
            font-size: 6rem;
            margin-bottom: 2rem;
        }
        .parrafo{
            font-size: 2rem;
            font-weight: 400;
            text-transform: capitalize;
            text-transform: uppercase;
            padding-right: 1rem;
          }
        .lema{
            font-size: 8rem;
            text-transform: capitalize;
            text-transform: uppercase;
            font-weight: 900;
          }
          .comprar{
            width: 30%;
            margin-top: 3rem;
            font-family: 'Rubik', sans-serif;
            margin-bottom: 1rem;
            font-weight: 400;
            background-color: #2EC4B6;
            border: none;
            color: #FDFFFC;
            border-radius: 1rem;
            cursor: pointer;
            width: 50%;
            height: 5rem;
            font-size: 2rem;
            text-transform: uppercase;
        }
    }
    
}