.imagenes--bottom{
    width: 100%;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    margin: 3rem 0;
    .imagenes--bottom--container{
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(15rem, 1fr));
        gap: 1rem;
        width: 90%;
        img{
            height: 100%;
            width: 100%;
        }
    }
}
@media (min-width: 625px) {
    .imagenes--bottom{
        .imagenes--bottom--container{
            grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr));
        }
    }
}
